import { graphql } from "gatsby";
import React from "react";
import { IQueryArrayResult, ICategory, IAuthor, IPost, ITag, IGatsbyLocation } from '../../interfaces';
import { ContentLayoutWithSidebar } from '../../components/Layout/ContentLayout';
import { SideBar } from "../../components/SideBar/SideBar";
import { nestCategories, getCategory, urlJoin } from '../../tool';
import { SideBarQuickstart } from "../../components/SideBar/SideBarQuickstart";
import { SideBarHelp } from "../../components/SideBar/SideBarHelp";
import SideBarCategories from "../../components/SideBar/SideBarCategories";
import { SideBarSearch } from "../../components/SideBar/SideBarSearch";
import { CategoryBar } from "../../components/CategoryBar/CategoryBar";
import { PostList } from '../../components/Post/PostList';
import { IHeaderProps } from "../../components/Layout/Header";

interface IQueryResult
{
  data: {
    posts: IQueryArrayResult<IPost>;
    categories: IQueryArrayResult<ICategory>
    authors: IQueryArrayResult<IAuthor>
    tags: IQueryArrayResult<ITag>
  };
  context: any;
  location: IGatsbyLocation;
}


export class Category extends React.Component<IQueryResult>
{
  constructor(props)
  {
    super(props);
  }
  render(){
    let posts = this.props.data.posts.edges
      .map(e => e.node)
      .sort((a,b) => {
        if(a.fields.timestamp > b.fields.timestamp)
          return -1
        else
          return 1;
      });
    let categories            = nestCategories(this.props.data.categories.edges.map(e => e.node));
    let authors               = this.props.data.authors.edges.map(e => e.node);
    let currentCategory       = getCategory(this.props.context.cat, categories);
    let currentCategoryString = currentCategory.friendlyname;
    let currentCategoryPath   = currentCategory.slug;
    let c                     = currentCategory.parent;
    let headerTags            = [currentCategory.friendlyname];

    while(c != null)
    {
      currentCategoryString = c.friendlyname + " / " + currentCategoryString;
      currentCategoryPath   = urlJoin(c.slug, currentCategoryPath)
      headerTags.push(c.friendlyname);

      c = c.parent;
    }

    let header: IHeaderProps = {
      url: urlJoin("category", currentCategoryPath),
      description: `Posts about ${currentCategory.friendlyname}`,
      type: "object",
      title: `${currentCategory.friendlyname} Archive | Powerpivot Insights | Durchblick durch Daten`,
      tags: headerTags
    }

    return (
      <ContentLayoutWithSidebar
      header={header} 
      sidebar={(
        <SideBar isFixed={true}>
          <SideBarQuickstart></SideBarQuickstart>
          <SideBarHelp></SideBarHelp>
          <SideBarSearch></SideBarSearch>
          <SideBarCategories></SideBarCategories>
        </SideBar>
      )} 
      >
      <CategoryBar title={"Kategorie / "+currentCategoryString}/>
      <PostList posts={posts} categories={categories} authors={authors}/>
      </ContentLayoutWithSidebar>
    )
  }
}

const Page = ({pageContext, data, location}) => {
  return (
    <Category data={data} location={location} context={pageContext}></Category>
  );
};
export default Page;

export const pageQuery = graphql`
  query categoryPosts($cat: String!, $now: Int!) {
    posts: allMarkdownRemark(
      filter: {
        fileAbsolutePath: {
          regex: "/.*/data/posts/.*[.]md/"
        }, 
        frontmatter: {
          categories: {
            eq: $cat
          }
        }
        fields: {
          publishTimestamp:{
            lt: $now
          }
        }
      }) {
      edges {
        node {
          ...postShort
        }
      }
    }
    ...categories
    ...authorsMarkdown
    ...tags
  }
`;
