import React from "react";
import "./SideBar.sass";
import { Link } from "gatsby";

export class SideBarSearch extends React.Component<{}>
{

  render()
  {
    return (
      <div className="sidebar-component">
        <div className="sidebar-header">
            Artikel mit Google durchsuchen
        </div>
        <div className="sidebar-content">
          <div className="sidebar-content-pad">
            <div style={{marginBottom: "5px"}}>
              <form action="https://www.google.com/search" className="searchform" method="get" name="searchform" target="_blank">
                <input name="sitesearch" type="hidden" value="powerbiinsights.de"/>
                <div style={{textAlign: "center", marginTop: "5px"}}>
                  <input  autoComplete="on" className="form-control search" name="q" placeholder="" required={true}  type="text"/>
                </div>
                <div style={{textAlign: "center", marginTop: "5px"}}>
                  <button className="button action-button" type="submit">Suchen</button>
              </div>
              </form>
            </div>
          </div>
          
        </div>
      </div>
    )
  }
}